.login-container {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #f5f5f5;
  padding: 20px;
}

.login-card {
  width: 100%;
  max-width: 400px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06);
  border-radius: 8px;
  background: #ffffff;
  border: 1px solid #e8e8e8;
}

.login-header {
  text-align: center;
  margin-bottom: 48px;
  padding-top: 20px;
}

.login-header .ant-typography {
  margin-bottom: 16px;
}

.login-card .ant-card-body {
  padding: 0 32px 32px;
}

.login-footer {
  text-align: center;
  margin-top: 24px;
}

.ant-form-item {
  margin-bottom: 24px;
}

.ant-btn-primary {
  height: 40px;
  font-size: 14px;
  border-radius: 6px;
  background: #1890ff;
  border-color: #1890ff;
  font-weight: 500;
}

.ant-btn-primary:hover {
  background: #40a9ff;
  border-color: #40a9ff;
}

.ant-input-affix-wrapper {
  height: 40px;
  border-radius: 6px;
  border: 1px solid #d9d9d9;
  display: flex;
  align-items: center;
  padding: 0 12px;
}

.ant-input-affix-wrapper:focus,
.ant-input-affix-wrapper-focused {
  border-color: #1890ff;
  box-shadow: 0 0 0 2px rgba(24, 144, 255, 0.2);
}

.ant-input {
  height: 40px;
  border-radius: 6px;
  border: none;
  box-shadow: none;
  padding: 0;
  background: transparent;
  line-height: 1.5;
}

.ant-input::placeholder {
  color: #bfbfbf;
  font-size: 14px;
  line-height: 1.5;
}

/* 确保输入框内容垂直居中 */
.ant-form-item-control-input-content {
  display: flex;
  align-items: center;
}

.ant-input-affix-wrapper .ant-input {
  display: flex;
  align-items: center;
  height: 100%;
}

.ant-btn-link {
  color: #1890ff;
  font-size: 14px;
}

.ant-btn-link:hover {
  color: #40a9ff;
}
