/* 整体布局 */
.ant-layout {
  background: #ffffff;
}

/* 侧边栏样式 */
.ant-layout-sider {
  background: #ffffff;
  border-right: 1px solid #e8e8e8;
  box-shadow: 2px 0 8px rgba(0, 0, 0, 0.02);
}

.logo {
  height: 64px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 0 24px;
  background: #ffffff;
  border-bottom: 1px solid #e8e8e8;
  font-size: 16px;
  font-weight: 600;
  color: #262626;
}

.logo .anticon {
  font-size: 20px;
  color: #1890ff;
  margin-right: 12px;
}

/* 菜单样式 */
.ant-menu-light {
  background: #ffffff;
  border-right: none;
  padding: 8px 0;
}

.ant-menu-light .ant-menu-item {
  margin: 2px 16px;
  border-radius: 8px;
  height: 44px;
  line-height: 44px;
  color: #595959;
  font-weight: 500;
  padding: 0 16px !important;
  width: calc(100% - 32px);
  overflow: hidden;
  position: relative;
}

.ant-menu-light .ant-menu-item:hover {
  background: #ffffff;
  color: #262626;
}

.ant-menu-light .ant-menu-item-selected {
  background: #ffffff !important;
  color: #1890ff !important;
  font-weight: 600;
  position: relative;
}

.ant-menu-light .ant-menu-item-selected::before {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 3px;
  height: 20px;
  background: #1890ff;
  border-radius: 0 2px 2px 0;
}

.ant-menu-light .ant-menu-item-selected::after {
  display: none;
}

.ant-menu-light .ant-menu-item .anticon {
  font-size: 16px;
  margin-right: 12px;
}

/* 头部样式 */
.dashboard-header {
  background: #ffffff;
  padding: 0 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #e8e8e8;
  height: 64px;
}

.header-right {
  display: flex;
  align-items: center;
}

.header-right .ant-avatar {
  background: #1890ff;
  margin-right: 8px;
}

.header-right span {
  color: #262626;
  font-weight: 500;
}

/* 内容区域 */
.dashboard-content {
  margin: 24px;
  padding: 0;
  background: transparent;
  min-height: calc(100vh - 112px);
}

/* 卡片样式 */
.ant-card {
  border-radius: 8px;
  border: 1px solid #e8e8e8;
  box-shadow: none;
  margin-bottom: 16px;
}

.ant-card-head {
  border-bottom: 1px solid #e8e8e8;
  padding: 0 24px;
}

.ant-card-head-title {
  color: #262626;
  font-weight: 600;
  font-size: 16px;
}

.ant-card-body {
  padding: 24px;
}

/* 统计卡片 */
.ant-statistic-title {
  color: #8c8c8c;
  font-size: 14px;
  margin-bottom: 4px;
}

.ant-statistic-content {
  color: #262626;
  font-size: 24px;
  font-weight: 600;
}

.ant-statistic-content-prefix {
  color: #1890ff;
  margin-right: 8px;
}

/* 表格样式 */
.ant-table {
  background: #ffffff;
}

.ant-table-thead > tr > th {
  background: #ffffff;
  border-bottom: 1px solid #e8e8e8;
  color: #262626;
  font-weight: 600;
  font-size: 14px;
}

.ant-table-tbody > tr > td {
  border-bottom: 1px solid #f0f0f0;
  color: #595959;
}

.ant-table-tbody > tr:hover > td {
  background: #ffffff;
}

/* 按钮样式 */
.ant-btn {
  border-radius: 6px;
  font-weight: 500;
  height: 32px;
  padding: 0 15px;
  font-size: 14px;
}

.ant-btn-primary {
  background: #1890ff;
  border-color: #1890ff;
}

.ant-btn-primary:hover {
  background: #40a9ff;
  border-color: #40a9ff;
}

.ant-btn-default {
  color: #595959;
  border-color: #d9d9d9;
}

.ant-btn-default:hover {
  color: #1890ff;
  border-color: #1890ff;
}

/* 标签样式 */
.ant-tag {
  border-radius: 4px;
  font-size: 12px;
  padding: 2px 8px;
  border: none;
}

/* 输入框样式 */
.ant-input {
  border-radius: 6px;
  border: 1px solid #d9d9d9;
}

.ant-input:focus,
.ant-input-focused {
  border-color: #1890ff;
  box-shadow: 0 0 0 2px rgba(24, 144, 255, 0.2);
}

/* 页面标题 */
.ant-typography h3 {
  color: #262626;
  font-weight: 600;
  margin-bottom: 24px;
}

/* 响应式设计 */
@media (max-width: 768px) {
  .dashboard-content {
    margin: 16px;
  }
  
  .ant-card-body {
    padding: 16px;
  }
  
  .logo span {
    display: none;
  }
  
  .header-right span {
    display: none;
  }
}

/* 折叠状态下的样式 */
.ant-layout-sider-collapsed .logo {
  justify-content: center;
  padding: 0;
  display: flex !important;
  align-items: center;
  height: 64px;
}

.ant-layout-sider-collapsed .logo .anticon {
  font-size: 20px;
  color: #1890ff;
  margin-right: 0;
  display: block !important;
}

.ant-layout-sider-collapsed .logo span {
  display: none;
}

.ant-layout-sider-collapsed .ant-menu-light .ant-menu-item {
  margin: 2px 8px;
  text-align: center;
  width: calc(100% - 16px);
  padding: 0 8px !important;
}

.ant-layout-sider-collapsed .ant-menu-light .ant-menu-item .anticon {
  margin-right: 0;
  font-size: 18px;
}

.ant-layout-sider-collapsed .ant-menu-light .ant-menu-item-selected::before {
  left: 2px;
  width: 2px;
  height: 16px;
}

/* 仪表盘表格行高统一 - 强制设置 */
.dashboard-table-row {
  height: 50px !important;
}

.dashboard-table-row td {
  height: 50px !important;
  vertical-align: middle !important;
  padding: 12px 16px !important;
  line-height: 1.4 !important;
}

.dashboard-table-row .ant-tag {
  margin: 0 !important;
  line-height: 20px !important;
  height: 20px !important;
  display: inline-flex !important;
  align-items: center !important;
  font-size: 12px !important;
  padding: 0 6px !important;
  vertical-align: middle !important;
}

/* 强制所有仪表盘小表格行高一致 */
.ant-table-small .dashboard-table-row {
  height: 50px !important;
}

.ant-table-small .dashboard-table-row td {
  height: 50px !important;
  padding: 12px 16px !important;
  vertical-align: middle !important;
}

/* 确保时间列的换行不影响行高 */
.dashboard-table-row td:last-child {
  white-space: nowrap !important;
  overflow: hidden !important;
  text-overflow: ellipsis !important;
}

/* 安全配置表单项标签颜色优化 */
.ant-form-item-label > label {
  color: #1890ff !important; /* 蓝色标签 */
  font-weight: 500 !important;
}

/* 仪表盘卡片标题颜色优化 */
.ant-card-head-title {
  color: #000000 !important; /* 黑色标题 */
  font-weight: 600 !important;
}
